<template>
    <div>
          <div class="container mt-6 mb-6">
            <div class="row justify-content-center">
                <div class="col-3" v-for="(item, index) in datanya" :key="index+'kategori'">
                    <div class="bg-white shadow rounded-lg p-2" > 
                        <div class="bg-white shadow rounded-lg p-2">
                                <p class="font-semibold text-red-500">{{item.title}}</p>
                            <img :src="item.gambar" style="width:100%;height:200px;" alt="">
                            <div class="p-3">
                                <p style="white-space:pre;font-size:13px;">
                                    {{item.fasilitas}}
                                </p>
                                <p style="white-space:pre;font-size:13px;">{{item.detail}}

                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default{
    layout:'app_landing_estate2',
    data(){
        return{
            datanya:[]
        }
    },
    methods: {
        getData(){
            sdb.collection("app_gis_fasilitas",false).doc().select(`select * from app_gis_fasilitas where idkategori=${this.$route.query.idkategori}`).then(res => {
                console.log(res);
                this.datanya=res;
                this.$forceUpdate();
            });
            
        }
    },
    mounted() {
        this.getData();
    },
}
</script>